import { Constants, Api, Helper } from "../utils";
const qs = require("qs");

const reset_password = async params => {
  return Api.post(
    Constants.URL_SOLICITAR_CONTRASENA,
    Helper.parseFormData(params)
  );
};
const change_password = async params => {
  return Api.post(
    Constants.URL_CAMBIAR_CONTRASENA,
    Helper.parseFormData(params)
  );
};
const login = async params => {
  return new Promise((resolve, reject) => {
    Api.post(Constants.URL_LOGIN, params)
      .then(response => {
        let { data } = response;
        let { success, token } = data;
        if (success) {
          Api.defaults.headers.Authorization = token;
        }
        resolve(response);
      })
      .catch(err => reject);
  });
};

export { login, reset_password, change_password };
