import React from "react";
import {
  BrowserRouter as Router,
  Route,
  Switch,
  withRouter
} from "react-router-dom";
import { Layout, Menu, Icon } from "antd";
import qs from "qs";
import "./mainlayout.scss";
import Header from "../../components/header/panel/panelHeader";
import Footer from "../../components/footer/Footer";
const { Content, Sider } = Layout;
const { SubMenu } = Menu;
const Logo = require("../../resources/images/logo.svg");

const MainLayout = props => {
  let { className, children, location } = props;
  let { search } = location;
  let query = qs.parse(search.replace("?", ""));
  let theme = query.theme || "light";
  return (
    <Layout className={`main-layout ${className || ""} ${theme}`}>
      <Sider collapsible className="side-bar">
        <div className="logo">
          <img src={Logo} />
          <span className="logo-title">DonecLab</span>
        </div>
        <Menu className="menu-sider" defaultSelectedKeys={["1"]} mode="inline">
          <Menu.Item key="1">
            <Icon type="dashboard" />
            <span>Dashboard</span>
          </Menu.Item>
          <Menu.Item key="2">
            <Icon type="database" />
            <span>Collections</span>
          </Menu.Item>
          <Menu.Item key="3">
            <Icon type="api" />
            <span>Plugins</span>
          </Menu.Item>
          <Menu.Item key="4">
            <Icon type="rise" />
            <span>Connection</span>
          </Menu.Item>
          <Menu.Item key="5">
            <Icon type="setting" />
            <span>Settings</span>
          </Menu.Item>
        </Menu>
      </Sider>
      <Layout>
        <Header />
        <Content className="main-content">{children}</Content>
        <Footer />
      </Layout>
    </Layout>
  );
};
export default withRouter(MainLayout);
