import React, { Component } from "react";
/* Redux Connect */
import { connect } from "react-redux";
import { bindActionCreators } from "redux";
import actions from "../../redux/actions";
import "./login.scss";
/* Import Custom Components */
import Layout from "../../layouts/standar/standerLayout";
import { Card, Row, Col, Icon, Tooltip, Button, Input, Checkbox } from "antd";
import Logo from "../../resources/images/logo-circle1.svg";
import { LoginForm } from "../../components";

class Login extends Component {
  componentDidMount() {
    const { actions } = this.props;
    actions.action_search(["1", "2", "3"]);
  }
  componentWillReceiveProps(nextProps) {
    let { data } = nextProps;
    console.log("Example Reducers Data: ", data);
  }
  onChange = e => {
    console.log(e);
  };
  render() {
    return (
      <Layout className="login-page">
        <div className={"card-login"}>
          <div className={"card-container"}>
            <Row>
              <Col>
                <img src={Logo} />
              </Col>
            </Row>
            <Row type="flex" justify="space-around" align="middle">
              <LoginForm />
            </Row>
          </div>
          <div className={"card-footer"}>
            <Button shape="circle" icon="github" />
          </div>
        </div>
      </Layout>
    );
  }
}
/* Mapping Actions to Props*/
const mapStateToProps = state => {
  const { data } = state;
  return {
    data
  };
};
/* Event Action Dispatch to Props */
const mapDispatchToProps = dispatch => ({
  actions: bindActionCreators(actions, dispatch)
});

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(Login);
